import React, { FunctionComponent } from 'react';

import ClassNames from './Media.module.scss';

interface Props {
    twitterUrl?: string;
    facebookUrl?: string;
    instagramUrl?: string;
}

const Media: FunctionComponent<Props> = ({ twitterUrl, facebookUrl, instagramUrl }) => {
    return (
        <ul className={ClassNames.Media}>
            {twitterUrl && (
                <li>
                    <a href={twitterUrl} className="fab fa-twitter" />
                </li>
            )}
            {facebookUrl && (
                <li>
                    <a href={facebookUrl} className="fab fa-facebook" />
                </li>
            )}
            {instagramUrl && (
                <li>
                    <a href={instagramUrl} className="fab fa-instagram" />
                </li>
            )}
        </ul>
    );
};

export default Media;
